/**
 *  登录 api
 *  @module base/api/login
 *  @author coder
 */

// PORTAL_PATH
import {
  PORTAL_PATH
} from '@/config'

// ajax
import ajax from '@/utils/ajax'

/**
 * 登录页面
 * @const
 * @type {String}
 * @default  '[PORTAL_PATH]/center/auth/signIn'
 */
export const SIGN_IN = PORTAL_PATH + '/center/auth/signIn'

/**
 * 登录页面
 * @param {object} [data] 发送键值对数据
 * @returns {promise}
 */
export function signIn(data) {
  return ajax({
    method: 'POST',
    headers: {
      "Content-Type": "application/x-www-form-urlencoded"
    },
    data: data,
    url: SIGN_IN
  })
}

/**
 * 退出登录
 * @const
 * @type {String}
 * @default  '[PORTAL_PATH]/center/auth/signOut'
 */
export const SIGN_OUT = PORTAL_PATH + '/center/auth/signOut'

/**
 * 退出登录
 * @param {object} [data] 发送键值对数据
 * @returns {promise}
 */
export function signOut(data) {
  return ajax({
    method: 'POST',
    headers: {
      "Content-Type": "application/x-www-form-urlencoded"
    },
    data: data,
    url: SIGN_OUT
  })
}
