<template>
    <div class="projHome" id="home">
        <div class="homeHeader" >
            <!-- <div class="logo">
                <img src="../assets/img/logo.png" width="100%" style="vertical-align: middle"/>
            </div> -->
            <div class="navBox">
                <ul :style="{width:doNavWidth+'px'}">
                    <li v-for="item in navList" :key="item.key"
                        :class="{actClass:(item.key===actNav?true:false)}"
                        @click="handleNavRouter(item)">
                        <span :class="item.icon" style="margin-right: 3px"></span>{{item.name}}
                    </li>
                </ul>
            </div>
            <div class="account">
                <el-dropdown trigger="click" @command="handleCommand">
                  <span class="el-dropdown-link">
                    <img src="../assets/img/people.png" width="30" class="img"/>
                    <span >{{ getUserInfo().account }}</span>
                  </span>
                  <el-dropdown-menu slot="dropdown">
                    <el-dropdown-item
                      command="个人中心"
                      icon="el-icon-edit"
                    >个人中心</el-dropdown-item>
                    <el-dropdown-item
                      command="系统日志"
                      icon="el-icon-date"
                    >系统日志</el-dropdown-item>
                    <el-dropdown-item
                      command="退出登录"
                      icon="el-icon-switch-button"
                    >退出登录</el-dropdown-item>
                  </el-dropdown-menu>
                </el-dropdown>
                <!-- <el-select v-model="value" placeholder="请选择" class="adminSize">
                    <el-option
                        v-for="item in options"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value">
                    </el-option>
                </el-select> -->
            </div>
            <el-button type="primary" size="mini" round style="position: fixed;right: 140px;top: 16px" @click="changeTheme">{{$root.themeHomeChange==='1'?'深色主题':'浅色主题'}}</el-button>
        </div>
        <div class="homeContent" ref="homeContent">
            <router-view ></router-view>
        </div>
    </div>
</template>

<script>
  import Interface from "@/base/mixin/login.js";
  export default {
    name: "index",
    mixins: [Interface],
    data() {
      return {
        options: [{
          value: 'admin',
          label: 'admin'
        }],
        value: 'admin',
        doNavWidth: '0',
        navList: [{
          name: '首页',
          key: '1',
          icon: 'el-icon-s-home',
          routerUrl: 'home'
        }, {
          name: '企管中心',
          key: '2',
          icon: 'el-icon-office-building',
          routerUrl: 'companyCenter'
        }, {
          name: '风控中心',
          key: '3',
          icon: 'el-icon-position',
          routerUrl: 'riskControlCenter'
        }, {
          name: '处置中心',
          key: '4',
          icon: 'el-icon-document',
          routerUrl: 'disposalCenter'
        }, {
          name: '评价中心',
          key: '5',
          icon: 'el-icon-edit',
          routerUrl: 'evaluationCenter'
        }],
        actNav: '1',
      }
    },
    created() {
      this.doNavWidth = this.navList.length * 200
      this.actNav = this.$route.query.actNav
    },
    mounted() {
    },
    methods: {
      // 用户操作
      handleCommand(val) {
        switch (!!val) {
          case val == '个人中心':
            // window.console.log('个人中心');
            this.actNav = 6;
            this.$router.push({
              path: '/personalCenter',
              query: {
                actNav: 6
              }
            })
            break;
          case val == '系统日志':
            // this.$message.success('-- 系统日志 --');
            this.actNav = 7;
            this.$router.push({
              path: '/systemLog',
              query: {
                actNav: 7
              }
            })
            break;
          case val == '退出登录':
            this.$confirm(`此操作将退出 <strong style="color: red;">${ this.getUserInfo().account }</strong> 账号 , 是否继续?`, '提示', {
              confirmButtonText: '确定',
              cancelButtonText: '取消',
              type: 'warning',
              dangerouslyUseHTMLString: true
            }).then(() => {
              // let { data } = await 
              this.$axios({
                method: "POST",
                url: "portal/center/auth/signOut",
              });
              // localStorage.clear();
              localStorage.removeItem("userInfo");
              localStorage.removeItem("bda.sec.http-intergrity.sign-response");
              localStorage.removeItem("bda.sec.http-intergrity.enabled");
              sessionStorage.removeItem("route")
              sessionStorage.removeItem("load")
              this.$router.push('/login')
              this.$message.success("已登出");
            }).catch(() => {});
            break;
          default:
            break;
        }
      },
      getUserInfo() {
        return JSON.parse(window.localStorage.getItem('userInfo')) || {}
      },
      // getScrollTop() {
      //   this.scrollTopVal = this.$refs.homeContent.scrollTop
      // },
      handleNavRouter(item) {
        this.actNav = item.key
        this.$router.push({
          path: item.routerUrl,
          query: {
            actNav: item.key
          }
        })
      },
      //切换主题
      changeTheme(){
        if (this.$root.themeHomeChange==='1'){
          document.getElementById('home').setAttribute('class', 'projHomeBlack')
          this.$root.themeHomeChange = '2'
        }else {
          document.getElementById('home').setAttribute('class', 'projHome')
          this.$root.themeHomeChange = '1'
        }
      },
    }
  }
</script>

<style lang="css">
  .el-dropdown-menu {
    min-width: 110px;
    border-radius: 5px !important;
    text-align: center;
  }
</style>

<style scoped lang="scss">
