        box-sizing: border-box;
        -moz-box-sizing: border-box; /* Firefox */
        -webkit-box-sizing: border-box; /* Safari */
        height: 100%;
        // min-width: 1600px;

        .homeHeader {
            width: 100%;
            height: 60px;
            line-height: 60px;
            background-image: linear-gradient(135deg, #3662CC 0%, #214499 100%);
