                ul {
                    width: 100%;
                    margin: 0 auto;
                    height: 100%;

                    li {
                        list-style: none; /* 将默认的列表符号去掉 */
                        padding: 0; /* 将默认的内边距去掉 */
                        margin: 0; /* 将默认的外边距去掉 */
                        float: left; /* 往左浮动 */
                        display: inline-block;
                        width: 200px;
                        /*width: 10vw;*/
                        line-height: 60px;
                        font-size: 16px;
                        /*font-size: 0.84vw;*/
                        color: #66B9FF;
                        font-weight: 700;
                        cursor: pointer;
                    }
                    .actClass {
                        background-color: #031E5F !important;
                        color: #ffffff!important;
                        opacity: 0.5;
                        transition: 1s;
                    }
                }
            }

            .account {
                width: 120px;

                .img {
                    vertical-align: middle
                }

                ::v-deep .adminSize {
                    vertical-align: middle;
                    width: 90px;

                    .el-input__inner {
                        border: none;
                        background-color: rgba(255, 255, 255, .0) !important;
                        color: #ffffff;
                        padding: 0 6px;
                    }

                    .el-select__caret {
                        display: none;
                    }
                }
            }

        }

        .homeContent {
            height: calc(100% - 60px);
            overflow: auto;
        }
        //按钮、输入框、下拉框等样式
        ::v-deep {
            /*.el-button{*/
            /*    background-color: #041338;*/
            /*    border: 1px solid #4389f9;*/
            /*    border-radius: 4px;*/
            /*    color: #fff;*/
            /*}*/
            .el-input__inner{
                background: #041338;
                border: 1px solid #4389f9 !important;
                color: #4298f3!important;
                border-radius: 4px;
            }
        }
    }
</style>