/**
 *  登录 mixin module
 *  @module base/mixin/login
 *  @author coder
 */
import {
  signIn,
  signOut
} from '@/base/api/login'

// mixin
export default {

  /**
   * 登录数据存储载体对象
   * @member login
   * @property {Array} [list=[]] 获取登录列表响应数据
   * @property {number} [page=null] 获取登录列表响应页码
   * @property {number} [limit=null] 获取登录列表页大小，每页记录条数
   * @property {number} [total=0] 获取登录列表数据记录总条数
   * @property {object} [model=null] 获取登录单条记录
   */
  data() {
    return {
      login: {
        list: [],
        page: null,
        limit: null,
        total: 0,
        model: null
      }
    }
  },
  methods: {

    /**
     * 登录页面
     * @function signIn
     * @param {object} data 请求发送的数据data
     * @returns {Promise} Promise实例
     */
    signIn(data) {
      return signIn(data).then(res => {
        return res
      })
    },
    /**
     * 退出登录
     * @function signOut
     * @param {object} data 请求发送的数据data
     * @returns {Promise} Promise实例
     */
    signOut(data) {
      return signOut(data).then(res => {
        return res
      })
    }
  }
}
